.work {
  z-index: 8;
  position: absolute;
  width: 100%;
  height: 100vh;
  opacity: 0;
  background: rgba(50, 50, 50, 0.9);
  background: linear-gradient(
    340deg,
    rgba(170, 170, 170, 0.5) 20%,
    rgba(190, 190, 190, 0.85) 40%,
    rgba(170, 170, 170, 0.5) 70%
  );
  backdrop-filter: blur(8px);
  pointer-events: none;
  overflow-y: scroll;
  overflow-x: hidden;
  transition: all 0.35s cubic-bezier(0.85, 0.135, 0.15, 0.5);
  transform: perspective(500px) translate3d(0, 3%, -40px);
  visibility: hidden;

  .container {
    position: relative;
  }

  .fucking-btn {
    position: absolute;
    z-index: 9;
    top: 1rem;
    right: 1rem;
  }

  &.active {
    opacity: 1;
    transform: perspective(0) translate3d(0, 0, 0);
    pointer-events: initial;
    visibility: initial;
  }

  h5 {
    text-transform: uppercase;
    font-weight: 300;
    font-size: 2rem;
    margin-top: 2rem;
    text-align: center;
  }

  .project {
    display: block;
    position: relative;
    margin: 1rem 0;
    box-shadow: 0 1rem 2rem 0 rgba(20, 20, 20, 0.1);
    transition: all 0.3s ease-in-out;

    img {
      width: 100%;
      height: auto;
    }

    .over {
      position: absolute;
      top: auto;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
      background: linear-gradient(
        190deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.8) 70%
      );
      transition: all 0.3s ease-in-out;

      .project-info {
        position: absolute;
        bottom: 2rem;
        left: 2rem;
        color: #333;
        font-size: 1.2rem;
        line-height: 1.2rem;

        span {
          color: #555;
          font-size: 0.8rem;
        }
      }
    }

    &:hover {
      margin-top: 0;
      box-shadow: 0 1rem 3rem 0 rgba(90, 90, 90, 0.5);

      .over {
        opacity: 1;
      }
    }
  }
}

@media only screen and (min-width: 980px) {
  .work {
    h5 {
      font-size: 3rem;
    }
  }
}
